body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
nav {
  padding: 0 15rem;
}

.products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  padding: 0 10%;
}

.card-image img {
  height: 150px;
  box-sizing: inherit;
}

.card {
  display: flex;
  flex-direction: column;
  margin: 0.5rem 0 1rem 0;
  border-radius: 2px;
  background-color: #fff;
}

.card-content {
  flex: 1;
}

.card-action {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: inherit;
  border-top: 1px solid rgba(160, 160, 160, 0.2);
  position: relative;
  padding: 16px 24px;
}

.icon {
  font-size: 2rem;
  color: orange;
}

.stars {
  padding-left: 20px;
}

.btn {
  font-size: 14px;
  width: 100px;
}

.price {
  font-size: 16px;
}

.card-title {
  font-family: "Roboto", sans-serif;
}

.card-category {
  font-weight: bold;
}

.header {
  background-color: black;
  padding: 30px;
  text-align: center;
}

a {
  color: white;
  font-size: 20px;
  padding: 0 10%;
}
